<template>
<!-- 视频直播 -->
    <div
        class="m-rank-video"
        v-loading="loading"
        element-loading-text="加载中..."
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
    >
        <div class="m-rank-video-title">
            <img :src="video_title_img" />
            <div class="u-extend">
                <el-select
                    class="u-server"
                    v-model="server"
                    placeholder="请选择服务器"
                    size="mini"
                >
                    <el-option key="all" label="全部" value=""></el-option>
                    <el-option
                        v-for="item in servers"
                        :key="item"
                        :label="item"
                        :value="item"
                    ></el-option>
                </el-select>
                <el-pagination
                    class="u-pages"
                    layout="prev, pager, next"
                    :hide-on-single-page="true"
                    :page-size="per"
                    :total="total"
                    :current-page.sync="page"
                >
                </el-pagination>
            </div>
        </div>
        <div class="m-rank-video-live" v-if="event_status && list && list.length">
            <div class="u-live">
                <iframe :src="live_url || default_live_url" frameborder="0"></iframe>
            </div>
            <div class="u-list">
                <li v-for="(item,i) in list" :key="i" @click="play(item)" :class="{on:!!item.isActive}">
                    <img class="u-live-logo" :src="item.team.logo | teamLogo" />
                    <span class="u-live-name">{{item.team.name}} <em>@{{ item.team.server }}</em> </span>
                </li>
            </div>
        </div>
        <div class="m-rank-video-content">
            <template v-if="data && data.length">
                <el-row class="m-rank-video-list" :gutter="20">
                    <el-col :span="8" v-for="(item, i) in data" :key="i">
                        <div class="m-rank-video-item">
                            <a
                                class="u-video"
                                :href="
                                    getTVlink(item.team.tv_type, item.team.tv)
                                "
                                target="_blank"
                            >
                                <template v-if="item.team.tv_type == 'douyu'">
                                    <img
                                        v-if="item.douyu.room_src"
                                        :src="item.douyu.room_src"
                                        class="u-live-cover"
                                        loading="lazy"
                                    />
                                    <img
                                        v-else
                                        :src="item.team.logo | teamLogo"
                                        class="u-live-null"
                                        loading="lazy"
                                    />
                                    <i
                                        class="u-status"
                                        :class="{
                                            on: ~~item.douyu.show_status == 1,
                                        }"
                                        ><i class="el-icon-video-camera"></i>
                                        {{
                                            liveStatusMap[
                                                ~~item.douyu.show_status
                                            ]
                                        }}</i
                                    >
                                    <i
                                        class="u-player"
                                        v-if="~~item.douyu.show_status == 1"
                                    >
                                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 511.999 511.999" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><linearGradient id="a" gradientUnits="userSpaceOnUse" x1="35.029" y1="257.313" x2="478.029" y2="257.313" gradientTransform="matrix(.9983 0 0 -.9983 -.085 512.866)"><stop offset="0" stop-color="#00f2fe"/><stop offset=".021" stop-color="#03effe"/><stop offset=".293" stop-color="#24d2fe"/><stop offset=".554" stop-color="#3cbdfe"/><stop offset=".796" stop-color="#4ab0fe"/><stop offset="1" stop-color="#4facfe"/></linearGradient><path d="M104.967 511.999l-.313-.001c-38.47-.21-69.77-31.983-69.77-70.829V70.058c0-25.691 13.39-48.512 35.819-61.041 22.429-12.531 48.88-11.971 70.758 1.497l302.4 186.405c20.869 12.848 33.305 35.16 33.255 59.678-.05 24.52-12.576 46.781-33.507 59.55L224.997 449.999c-9.405 5.758-21.695 2.802-27.452-6.602-5.758-9.404-2.802-21.694 6.602-27.452l218.639-133.869c9.139-5.576 14.378-14.886 14.4-25.56.022-10.673-5.179-20.004-14.268-25.601L120.517 44.511c-9.374-5.771-20.718-6.011-30.339-.636-9.62 5.375-15.364 15.162-15.364 26.183V441.17c0 18.385 15.541 30.819 30.056 30.898 4.735.001 10.08-1.726 15.553-5.064 9.411-5.743 21.698-2.768 27.441 6.645 5.743 9.413 2.767 21.698-6.645 27.441-11.702 7.14-24.23 10.908-36.252 10.909z" fill="url(#a)"/></svg>
                                    </i>
                                </template>
                                <template v-else>
                                    <img
                                        :src="item.team.logo | teamLogo"
                                        class="u-live-null"
                                        loading="lazy"
                                    />
                                    <i class="u-status"
                                        ><i class="el-icon-warning-outline"></i>
                                        未知</i
                                    >
                                </template>
                            </a>
                            <div class="u-info">
                                <img
                                    :src="item.team.logo | liveAvatar"
                                    class="u-team-logo"
                                    loading="lazy"
                                />
                                <div class="u-team">
                                    <span class="u-label">团队 : </span>
                                    <a
                                        class="u-team-name"
                                        :href="item.team.ID | teamLink"
                                        target="_blank"
                                        >{{ item.team.name }}</a
                                    >
                                </div>
                                <div class="u-room">
                                    <span class="u-label">房间 : </span>
                                    <a
                                        class="u-room-name"
                                        :href="
                                            getTVlink(
                                                item.team.tv_type,
                                                item.team.tv
                                            )
                                        "
                                        target="_blank"
                                    >
                                        {{
                                            (item.team.tv_type == "douyu" &&
                                                item.douyu.room_name) ||
                                                item.team.name + "的直播间"
                                        }}
                                    </a>
                                </div>
                            </div>
                        </div>
                    </el-col>
                </el-row>
                <el-pagination
                    class="m-rank-video-pages m-archive-pages"
                    background
                    layout="total, prev, pager, next,jumper"
                    :hide-on-single-page="true"
                    :page-size="per"
                    :total="total"
                    :current-page.sync="page"
                >
                </el-pagination>
            </template>
            <el-alert
                v-else
                class="m-archive-null"
                title="没有找到相关条目"
                type="info"
                center
                show-icon
            >
            </el-alert>
        </div>
    </div>
</template>

<script>
import { __imgPath } from "@jx3box/jx3box-common/data/jx3box.json";
import { getLives } from "@/service/video.js";
import { getThumbnail,getLink } from "@jx3box/jx3box-common/js/utils";
import { default_avatar } from "@jx3box/jx3box-common/data/jx3box.json";
import getTVlink from "@/assets/js/tv.js";
const liveStatusMap = ["等待开播", "直播中", "直播结束"];
import servers from "@jx3box/jx3box-data/data/server/server_list.json";
export default {
    props: [],
    data: function() {
        return {
            video_title_img: __imgPath + "image/rank/common/lives.png",
            data: [],
            per: 24,
            page: 1,
            total: 1,
            liveStatusMap,
            loading: false,
            servers,
            server: "",
            live_url : ''
        };
    },
    computed: {
        id: function() {
            return this.$store.state.id;
        },
        params: function() {
            return {
                pageSize: this.per,
                pageIndex: this.page,
                server: this.server,
            };
        },
        list: function() {
            let list = [];
            this.data?.forEach((item, i) => {
                if (
                    item.team.tv_type == "douyu" &&
                    item.douyu.show_status == 1
                ) {
                    list.push(item);
                }
                if(i == 0){
                    item.isActive = true
                }
            });
            return list;
        },
        default_live_url : function (){
            if(this.list && this.list.length){
                return 'https://open.douyu.com/tpl/h5/chain2/jdxoubyoux/' + this.list[0]['team']['tv']
            }else{
                return ''
            }
        },
        event_status : function (){
            return !!this.$store.state.race.status || false
        }
    },
    methods: {
        getTVlink,
        loadData: function() {
            this.loading = true;
            getLives(this.id, this.params)
                .then((res) => {
                    this.data = res.data.data.list;
                    // this.page = res.data.data.page.index;
                    // this.total = res.data.data.page.total;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        play : function (item){
            this.live_url = 'https://open.douyu.com/tpl/h5/chain2/jdxoubyoux/' + item.team.tv
            this.clean()
            item.isActive = true
        },
        clean : function (){
            this.list?.forEach((item) => {
                item.isActive = false
            })
        }
    },
    watch: {
        params: {
            deep: true,
            handler: function() {
                this.loadData();
            },
        },
    },
    filters: {
        teamLogo: function(val) {
            return val ? getThumbnail(val, 120, true) : default_avatar;
        },
        liveAvatar: function(val) {
            return val ? getThumbnail(val, 68, true) : default_avatar;
        },
        teamLink: function(val) {
            // return "/team/#/org/view/" + val;
            return getLink('org',val);
        },
    },
    created: function() {
        this.loadData();
    },
    components: {},
};
</script>

<style lang="less">
@import "../assets/css/race_video.less";
</style>
